<!-->用户注册修改密码页面<-->
<template>
  <div id="personalDefault">
  </div>
</template>

<script>
export default {
  name: "personalDefault",

  data() {
    return {
      currentPath: "first",
      active: 2
    };
  },
  methods: {
    init() {
      //显示路径
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == "个人中心"
      );
      global.currentPath.data = [index, 0];
    }
  },
  activated() {
    this.init();
  },
  mounted() {}
};
</script>


<style lang="scss" scoped>
#personalDefault {
  height: 100%;
  width: 1200px;
  padding: 50px;
  margin: 10px auto;
  background: rgba(235, 240, 247, 0.932);
  border-radius: 2px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.steps {
  margin-left: 30px;
  margin: 20px 0 50px 30px;
}
</style>



